import {connect} from 'react-redux'
import React,{Component} from 'react'
import {goodslist} from './Goods'
import { Route,withRouter } from 'react-router-dom'
import Carousel from './CarouselApp'
import SearchBar from './SerachBarApp'
import './home.css'
import Mytab from './Mytab'




const Home=(props)=>{
  props.dispatch(goodslist());
  return(
    <div>
      <Route>
        <SearchBar/>
        <Carousel/> 
        <Mytab/>      
       </Route>
      {props.goods.map((item)=>{
          return( <div 
            onClick={()=>{
              props.history.push({
                pathname:'/details',
                src:item.img,
                id:item.id,
                title:item.title
              })
            }}
            className="ho-img">
            <img src={item.img}/>
            <a className='money'>￥{item.price}</a>
            <a className='person'>{item.title}</a>
          </div>  
        )
      })
      }

    </div>
  )
}
const mapStateToProps=(state)=>({
  goods:state.goodlist.goods
})
export default connect(mapStateToProps)(Home)
