import React,{Component} from 'react'
import { connect } from 'react-redux';
import { Route,withRouter } from 'react-router-dom'
// import Tab from './MyTab1'
import MyTab from './Mytab'

const todos=(props)=>{
    console.log(props)
    return (
        <div>
            {props.mydata.map((item,idx)=>(
                <li key={item} style={{listStyleType:'none'}}>
                    <img src={item.img} style={{width:'100px',height:'100px'}}/>
                    <span style={{marginTop:'30PX'}}>{item.title}</span>
                    <button
                    onClick={(e)=>{
                        props.dispatch({
                            type:'DEL_TODO',
                            index:idx
                        })
                    }}
                    style={{width:'50px',height:'50px',backgroundColor:'#999',margin:"",float:'right'}}
                    >删除</button>
                </li>
            ))}
            <Route>
               <MyTab/>
           </Route>
        </div>
    )
}

const mapStateToProps=(state)=>{
    return{
        mydata:state.todo
    }
}

export default connect(mapStateToProps)(todos)
