import React,{useState} from 'react';
import {Button,NavBar,Icon,TabBar} from "antd-mobile";
// import  {withRouter}  from 'react-router-dom';
import {connect} from 'react-redux'

class Mytab1 extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        selectedTab: 'redTab',
        hidden:false,
        fullScreen:false
      };
    }
  componentDidMount(){
    let id=this.props.location.id
    console.log(this.props.todo);
    this.props.todo.map((item)=>{
      if(item.id==id){
        this.setState({
          selectedTab:'blueTab',
        })
      }
    })
    console.log(this.props.location)
  }

   
    render() {

      const { pathname } = this.props.location;
      console.log(pathname.split('/'));
      if(pathname.split('/').length>2){
          return null;
      }
      
      let img=this.props.location.src
      let id=this.props.location.id
      let title=this.props.location.title
      return (

        <div>
          <NavBar mode="light"
          icon={<Icon type="left"/>}
          onLeftClick={()=>this.props.history.push('/home')}
          rightContent={
            <button style={{border:0,background:'white'}} onClick={()=>{this.props.history.push('/home')}}>取消
            </button>
          }>宝贝详情
          </NavBar>
          <img style={{width:'100%'}} src={this.props.location.src}/>
        <p>{this.props.location.title}</p>
        {console.log(this.props.location)}
        <div style= {{ position: 'fixed', 
        width: '100%', 
        bottom: 0}}>

          <TabBar
            unselectedTintColor="#999"
            tintColor="#33A3F4"
            barTintColor="white"
          >
            <TabBar.Item
              Style={{width:'10px'}}
              icon={
              <i style={{fontSize:25, width:'60%',marginLeft:'-50%'}} className='iconfont icon-dianpu'></i>
              }
              // selectedIcon={
              // <i style={{fontSize:25}} className='iconfont icon-shouye'></i>
              // }
              
              title="商铺"
              key="Shangpu"
              selected={pathname === '/shangpu'}
              onPress={() => {
                this.props.history.push('/shangpu');
              }}
              data-seed="logId"
            >
  
            </TabBar.Item>
            <TabBar.Item
              icon={
              <i style={{fontSize:25}} className='iconfont icon-kefu'></i>
              }
              // selectedIcon={
              //   <i style={{fontSize:25}} className='iconfont icon-weitao'></i>

              // }
              title="客服"
              key="Kefu"

              selected={pathname=== '/kefu'}
              onPress={() => {
                this.props.history.push('/kefu')
              }}
            >
            </TabBar.Item>
            <TabBar.Item
              onClick={()=>{
                console.log(this.props)
                if(this.state.selectedTab==='blueTab'){
                  this.state.selectedTab=null
                }
              }}
              icon={
                <i style={{fontSize:25}} className='iconfont icon-shoucang'></i>
              }
              selectedIcon={
                <i style={{fontSize:25}} className='iconfont icon-shoucang1'></i>
              }
              
              onPress={()=>{
                this.props.dispatch({
                  type:'ADD_TODO',
                  col:{img:img,id:id,title:title}
                })
                this.setState({
                  selectedTab:'blueTab'
                })
              }}
              selected={this.state.selectedTab==='blueTab'}
              title="收藏"
              key="Shoucang"
             
            >

            </TabBar.Item>
            <TabBar.Item
            style={{width:'500%'}}
              key="Putincart"
              selected={pathname === '/putincart'}
              onPress={() => {
              this.props.history.push('/putincart')
              }}
              icon={
                <button style={{
                fontSize:'17px',
                backgroundColor:'#ffc500',
                width:'100px',
                height:'50px',
                color:'white',
                border:'0',
                borderRadius:'2rem 0 0 2rem'}}>加入购物车</button>
              }
            >
            </TabBar.Item>
            <TabBar.Item
            Style={{paddingLeft:'10px',width:'150%'}}
              key="Buyitnow"
              selected={pathname === '/buyitnow'}
              onPress={() => {
                this.props.history.push('/buyitnow')
              }}
              icon={
                <button style={{
                fontSize:'17px',
                backgroundColor:'#ff0023',
                color:'white',
                width:'100px',
                height:'50px',
                border:'0',
                borderRadius:'0 2rem 2rem 0'}}>立即购买</button>
              }
              >
                
            </TabBar.Item>
          </TabBar>
        </div>
        </div>
      );
    }
  }
  const mapStateToProps=(state)=>({
    todo:state.todo
  })

  export default connect (mapStateToProps)(Mytab1)
// export default withRouter (Mytab1);


