import { render } from "@testing-library/react"
import React,{useState} from "react"
import {login} from './login'
import {connect} from 'react-redux'

 const Collect =(props)=>{
    const [isShow,setShow]=useState(false); 
    const log=()=>{
        props.dispatch(login())       
    }
    return(
        <div>
            <input type="text" placeholder="输入用户名"/>用户名
            <br/>
            <input type="password" placeholder="输入密码"/>密码
            <br/>
            {props.username?
            <div>
            {localStorage.setItem('userinfo',props.username),props.history.push('/')}
            </div>
            :<button onClick={log}>登录</button>}
        </div>
    )
}
const mapStateToProps=(state)=>({
    username:state.home.username
})
export default connect(mapStateToProps)(Collect)